// icons
@font-face {
    font-family: $font-family-icon;
    src: url('#{font-path}icomoon.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: $font-family-icon !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

// lato
@font-face {
    font-family: 'lato';
    src: url('#{font-path}lato-light-webfont.woff') format('woff');
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: 'lato';
    src: url('#{font-path}lato-regular-webfont.woff') format('woff');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'lato';
    src: url('#{font-path}lato-bold-webfont.woff') format('woff');
    font-weight: 700;
    font-style: normal;
}